.form-section {
  padding: 30px 15px 0;
  background: url(./img/form/bg.svg) #e5e5e5;
  background-position: -350px 50%;
  background-repeat: no-repeat;
  @include media-breakpoint-up(lg) {
    background-position: 0px 50%;
    padding: 135px 15px 160px;
  }
}
.form {
  @include media-breakpoint-up(lg) {
    display: flex;
    justify-content: center;
  }
  &__form {
    @include media-breakpoint-up(lg) {
      max-width: 350px;
    }
  }
  &__submit {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 24px;
    padding-bottom: 110px;
  }
  &__field {
    border: 2px solid #181818;
    border-radius: 5px;
    width: 100%;
    font-family: "Poppins", sans-serif;
    font-size: 14px;
    line-height: 22px;
    color: #6e7378;
    padding: 11px 20px;
    background-color: #f5f5f7;

    & + .form__field {
      margin-top: 16px;
    }
    &_text-area {
      height: 175px;
      width: 100%;
    }
  }
  &__btn {
    width: 170px;
    height: 44px;
    background: #181818;
    border-radius: 5px;
    border: 0;
    font-weight: 600;
    font-size: 14px;
    line-height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    color: #fff;
    cursor: pointer;
    user-select: none;
    &-icon {
      margin-left: 12px;
    }
  }
  &__content {
    display: none;
    max-width: 300px;
    @include media-breakpoint-up(lg) {
      display: block;
      margin-left: 125px;
    }
  }
  &__caption {
    font-weight: bold;
    font-size: 26px;
    line-height: 40px;
    color: #181818;
    margin-bottom: 28px;
  }
}
.form-list {
  list-style-type: none;
  &__item {
    font-size: 16px;
    line-height: 26px;
    display: flex;
    align-items: flex-start;
    color: #181818;
  }
  &__box {
    margin-top: 10px;
    flex-shrink: 0;
    display: block;
    width: 6px;
    margin-right: 16px;
    height: 6px;
    background-color: #181818;
  }
  &__item + &__item {
    margin-top: 24px;
  }
}

.checkbox {
  display: flex;
  &__real {
    display: none;
  }
  &__real:checked ~ &__new {
    background-color: #181818;
  }
  &__real:checked ~ &__new &__svg {
    opacity: 1;
  }
  &__new {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 21px;
    height: 21px;
    border-radius: 2px;
    border: 2px solid #181818;
    transition: all 0.2s linear;
    cursor: pointer;
    &:hover {
      border-color: #6e7378;
    }
  }
  &__svg {
    opacity: 0;
    transition: all 0.25s linear;
  }
  &__text {
    font-weight: 600;
    font-size: 14px;
    line-height: 22px;
    text-transform: uppercase;
    margin-left: 10px;
    user-select: none;
  }
}
