.works {
  background-color: #e5e5e5;
  padding: 40px 0 120px;
  &__heading {
    font-weight: 800;
    font-size: 28px;
    line-height: 44px;
    text-align: center;
    color: #181818;
    max-width: 315px;
    margin: 0 auto 55px;
    @include media-breakpoint-up(lg) {
      font-size: 48px;
      line-height: 76px;
      margin-bottom: 65px;
      max-width: 375px;
    }
  }
}
.work-card {
  background-color: #fff;
  border-radius: 6px;
  overflow: hidden;
  &__img {
    width: 100%;
    &_soon {
      filter: blur(4px);
    }
    @include media-breakpoint-up(lg) {
      width: 50%;
    }
  }
  &__caption {
    font-weight: bold;
    font-size: 22px;
    line-height: 36px;
    color: #181818;

    @include media-breakpoint-up(lg) {
      font-size: 26px;
      line-height: 40px;
      margin-bottom: 16px;
    }
  }
  &__content {
    padding: 24px 24px 48px;
    margin-bottom: 8px;
    @include media-breakpoint-up(lg) {
      margin-bottom: 0;
      width: 50%;
      padding: 32px;
      display: flex;
      flex-direction: column;
    }
  }
  &__logo {
    display: none;
    margin-right: auto;
    margin-bottom: 16px;
    @include media-breakpoint-up(lg) {
      display: block;
    }
  }
  &__text {
    max-width: 63.5%;
    font-size: 14px;
    line-height: 22px;
    color: #181818;
    margin-bottom: 16px;
    &_soon {
      color: #6e7378;
    }
    @include media-breakpoint-up(lg) {
      height: 100%;
      max-width: 70%;
      font-size: 16px;
      line-height: 26px;
    }
  }
  &__tags {
    max-width: 70%;
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    margin-bottom: 55px;
    @include media-breakpoint-up(lg) {
      margin-bottom: 0;
      max-width: 100%;
    }
  }
  &__view-work {
    background: #181818;
    border-radius: 5px;
    font-weight: 600;
    font-size: 12px;
    line-height: 20px;
    color: #fff;
    padding: 10px 24px;
    text-decoration: none;
    &:active {
      background: #6e7378;
    }
    @include media-breakpoint-up(lg) {
      display: none;
    }
  }
  & + .work-card {
    margin-top: 25px;
  }
  @include media-breakpoint-up(lg) {
    display: flex;
    flex-direction: row-reverse;
  }
}

.work-tag {
  display: inline-block;
  font-size: 12px;
  line-height: 20px;
  color: #181818;
  background: #f0f0f0;
  border-radius: 4px;
  padding: 4px 16px;
  @include media-breakpoint-up(lg) {
    font-size: 14px;
    padding: 8px 16px;
    line-height: 1em;
  }
}
