.header {
  padding: 0 15px;
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  min-height: -webkit-fill-available;
  // height: calc(var(--vh, 1vh) * 100);
  background-color: #fff;
  position: relative;

  &__top {
    display: flex;
    justify-content: space-between;
    height: 80px;
    align-items: center;
  }
  &__caption {
    font-weight: 800;
    font-size: 36px;
    color: #181818;
    line-height: 58px;
    text-align: center;
    margin-bottom: 24px;
    @include media-breakpoint-up(lg) {
      font-weight: 800;
      font-size: 72px;
      line-height: 90px;
      max-width: 930px;
    }
  }
  &__sub-caption {
    font-size: 16px;
    line-height: 26px;
    text-align: center;
    color: #181818;
    margin-bottom: 40px;
    @include media-breakpoint-up(lg) {
      font-size: 24px;
      line-height: 38px;
      margin-bottom: 55px;
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: calc(100% - 80px);
  }
  &__meeting {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 220px;
    height: 55px;
    background: #ffe43c;
    border-radius: 5px;
    border: 0;
    font-weight: 600;
    font-size: 14px;
    line-height: 22px;
    text-transform: uppercase;
    color: #181818;
    cursor: pointer;
  }
  &__arrow-icon {
    margin-left: 20px;
  }
  & .container {
    height: 100%;
  }
}
.nav {
  width: 100%;
  opacity: 0;
  z-index: -1;
  background-color: #fff;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: calc(100% - 80px);
  left: 0;
  bottom: 0;
  transition: all 0.3s linear;
  &_active {
    z-index: 10;
    opacity: 1;
  }
  &__link {
    font-weight: 600;
    font-size: 22px;
    line-height: 36px;
    color: #181818;
    text-decoration: none;
    & + .nav__link {
      margin-top: 40px;
    }
  }
  &__hireus {
    @extend .header__meeting;
    width: 150px;
    font-size: 14px;
    margin-top: 70% !important;
    @include media-breakpoint-up(sm) {
      margin-top: 40% !important;
    }
    @include media-breakpoint-up(md) {
      margin-top: 30% !important;
    }
  }
  &-toggle {
    position: relative;
    width: 32px;
    height: 32px;
    background-color: transparent;
    border: 0;
    padding: 0;
    @include media-breakpoint-up(lg) {
      display: none;
    }
    &_active {
      & .nav-toggle-item:nth-child(2) {
        transform: translateX(-100%) translateY(-50%);
        opacity: 0;
      }
      & .nav-toggle-item:nth-child(1) {
        transform: translateX(-50%) translateY(-50%) rotate(45deg);
      }
      & .nav-toggle-item:nth-child(3) {
        transform: translateX(-50%) translateY(-50%) rotate(-45deg);
      }
    }
  }
  &-toggle-item {
    position: absolute;
    display: block;
    width: 24px;
    height: 4px;
    background-color: #000;
    border-radius: 4px;
    left: 50%;
    top: 50%;
    transition: all 0.2s ease;
    transform: translateX(-50%) translateY(-50%);
    &:first-child {
      transform: translateX(-50%) translateY(calc(-50% - 8px));
    }
    &:last-child {
      transform: translateX(-50%) translateY(calc(-50% + 8px));
    }
  }
  @include media-breakpoint-up(lg) {
    display: none;
  }
}
.desktop-nav {
  display: none;
  &__link {
    font-weight: 600;
    font-size: 14px;
    line-height: 22px;
    color: #181818;
    margin: 0 15px;
    text-decoration: none;
    transition: all 0.25s linear;

    &:hover {
      color: #ffe43c;
    }
  }
  @include media-breakpoint-up(lg) {
    display: block;
  }
}
