.numbers {
  padding: 80px 0 92px;
  text-align: center;
  background-color: #ffe43c;
  @include media-breakpoint-up(xl) {
    padding: 112px 0 144px;
  }
  &__heading {
    display: none;
    color: #181818;
    margin-bottom: 64px;
    @include media-breakpoint-up(md) {
      display: block;
      font-weight: 800;
      font-size: 38px;
      line-height: 60px;
    }
    @include media-breakpoint-up(xl) {
      display: block;
      font-weight: 800;
      font-size: 48px;
      line-height: 76px;
    }
  }
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include media-breakpoint-up(md) {
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-start;
    }
  }
  &__item {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    &:not(:last-child) {
      margin-bottom: 40px;
      @include media-breakpoint-up(md) {
        margin-bottom: 0;
      }
    }
    @include media-breakpoint-up(md) {
      width: 20%;
    }
  }
}

.numbers-item {
  &__number {
    font-weight: 800;
    font-size: 36px;
    line-height: 58px;
    color: #181818;
    margin-bottom: 8px;
    @include media-breakpoint-up(xl) {
      font-weight: bold;
      font-size: 32px;
      line-height: 52px;
      margin-bottom: 16px;
    }
  }
  &__text {
    font-weight: normal;
    font-size: 14px;
    line-height: 22px;
    color: #181818;
    @include media-breakpoint-up(xl) {
      font-weight: normal;
      font-size: 16px;
      line-height: 26px;
      max-width: 120px;
    }
  }
}
