.trust {
  padding: 112px 15px;
  background-color: #e5e5e5;
  @include media-breakpoint-up(md) {
    padding: 112px 0px;
  }
  @include media-breakpoint-up(xl) {
    padding: 152px 0px 0px;
  }
  &__heading {
    font-weight: 800;
    font-size: 28px;
    line-height: 44px;
    color: #181818;
    margin-bottom: 48px;
    text-align: center;
    @include media-breakpoint-up(md) {
      font-size: 38px;
      line-height: 60px;
    }
    @include media-breakpoint-up(xl) {
      font-weight: 800;
      font-size: 48px;
      line-height: 76px;
      margin-bottom: 64px;
    }
  }
  &__offers {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include media-breakpoint-up(md) {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }
  &__card {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-color: #fff;
    padding: 32px 24px 40px;
    border-radius: 3px;
    &:not(:last-child) {
      margin-bottom: 16px;
    }
    @include media-breakpoint-up(md) {
      width: calc(50% - 10px);
      margin-bottom: 16px;
      height: 260px;
      align-items: flex-start;
      text-align: left;
    }
    @include media-breakpoint-up(lg) {
      width: calc(33.3% - 10px);
      padding: 32px 15px 40px;
    }
    @include media-breakpoint-up(xl) {
      width: calc(33.3% - 10px);
      padding: 32px 24px 40px 30px;
      height: 250px;
    }
  }
}

.trust-card {
  &__icon {
    width: 42px;
    height: 42px;
    margin-bottom: 20px;
  }
  &__title {
    font-weight: 600;
    font-size: 18px;
    line-height: 28px;
    margin-bottom: 16px;
    color: #181818;
    @include media-breakpoint-up(md) {
      font-size: 16px;
      line-height: 26px;
      margin-bottom: 8px;
    }
    @include media-breakpoint-up(xl) {
      font-weight: 600;
      font-size: 18px;
      line-height: 30px;
    }
  }
  &__text {
    font-weight: normal;
    font-size: 14px;
    line-height: 22px;
    color: #181818;
  }
}
