.footer {
  width: 100%;
  background-color: #181818;
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 56px 15px 72px;
    @include media-breakpoint-up(lg) {
      padding: 48px 0px 42px;
    }
  }

  &__logo {
    margin-bottom: 80px;
    @include media-breakpoint-up(lg) {
      margin-bottom: 40px;
    }
  }

  &__menu {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 80px;
    @include media-breakpoint-up(lg) {
      flex-direction: row;
      margin-bottom: 40px;
    }
  }
  &__item:not(:last-child) {
    margin-bottom: 40px;
    @include media-breakpoint-up(lg) {
      margin-bottom: 0;
      margin-right: 34px;
    }
  }
  &__link {
    text-decoration: none;
    font-weight: 600;
    font-size: 14px;
    line-height: 22px;
    color: #ffffff;
    text-transform: uppercase;
    transition: all 0.25s linear;
    &:hover {
      color: #ffe43c;
    }
    @include media-breakpoint-up(lg) {
      color: #6e7378;
    }
  }
  &__mail {
    text-decoration: none;
    font-weight: normal;
    font-size: 14px;
    line-height: 22px;
    color: #ffffff;
    transition: all 0.25s linear;
    &:hover {
      color: #ffe43c;
    }
  }
  &__line {
    width: 100%;
    height: 1px;
    background-color: #6e7378;
  }
  &__copyring {
    padding: 32px 24px 46px;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    @include media-breakpoint-up(lg) {
      padding: 25px 0 38px;
    }
  }
  &__text {
    font-weight: normal;
    font-size: 12px;
    line-height: 20px;
    color: #6e7378;
    user-select: none;
  }
}
