.yes {
  background-color: #ffe43c;
  padding: 90px 15px 80px;
  @include media-breakpoint-up(md) {
    padding: 90px 0px 80px;
  }
  @include media-breakpoint-up(xl) {
    padding: 115px 0px 145px;
  }
  &__heading {
    font-weight: 800;
    font-size: 28px;
    line-height: 44px;
    color: #181818;
    margin-bottom: 40px;
    @include media-breakpoint-up(md) {
      max-width: 550px;
      font-size: 38px;
      line-height: 60px;
      margin-bottom: 60px;
    }
    @include media-breakpoint-up(lg) {
      font-weight: 800;
      font-size: 48px;
      line-height: 76px;
      margin-bottom: 80px;
      max-width: 700px;
    }
    @include media-breakpoint-up(xl) {
      max-width: 700px;
    }
  }
  &__approaches {
    display: flex;
    flex-direction: column;
    width: 100%;
    @include media-breakpoint-up(lg) {
      flex-direction: row;
      justify-content: space-between;
    }
  }
  &__card {
    display: flex;
    flex-direction: column;
    &:not(:last-child) {
      margin-bottom: 65px;
      @include media-breakpoint-up(lg) {
        margin-bottom: 0;
      }
    }
    @include media-breakpoint-up(md) {
      max-width: 500px;
    }
    @include media-breakpoint-up(lg) {
      width: 33.3%;
      max-width: 250px;
    }
    @include media-breakpoint-up(xl) {
      max-width: 325px;
    }
  }
}

.approaches-card {
  &__icon {
    width: 168px;
    height: 155px;
    margin-bottom: 25px;
    @include media-breakpoint-up(lg) {
      margin-bottom: 35px;
    }
  }
  &__title {
    font-weight: bold;
    font-size: 22px;
    line-height: 36px;
    margin-bottom: 8px;
    color: #181818;
    @include media-breakpoint-up(lg) {
      font-size: 20px;
      margin-bottom: 16px;
    }
    @include media-breakpoint-up(xl) {
      font-size: 26px;
    }
  }
  &__text {
    font-weight: normal;
    font-size: 16px;
    line-height: 26px;
    color: #181818;
    width: 100%;
    @include media-breakpoint-up(lg) {
      font-size: 14px;
      line-height: 22px;
    }
    @include media-breakpoint-up(xl) {
      font-size: 16px;
      line-height: 26px;
      max-width: 310px;
    }
  }
}
