.cover {
  padding: 80px 15px;
  background-color: #e5e5e5;
  text-align: center;
  .container {
    @include media-breakpoint-up(md) {
      padding: 0;
    }
  }
  @include media-breakpoint-up(md) {
    padding: 80px 0px;
  }
  @include media-breakpoint-up(xl) {
    padding: 152px 0px;
  }
  &__heading {
    font-weight: 800;
    font-size: 28px;
    line-height: 44px;
    color: #181818;
    margin-bottom: 56px;
    @include media-breakpoint-up(xl) {
      font-weight: 800;
      font-size: 48px;
      line-height: 76px;
      margin-bottom: 64px;
    }
  }
  &__services {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include media-breakpoint-up(md) {
      flex-direction: row;
      justify-content: space-between;
    }
  }
  &__card {
    width: 100%;
    background-color: #ffffff;
    border-radius: 3px;
    padding: 32px 40px 49px;
    &:not(:last-child) {
      margin-bottom: 16px;
      @include media-breakpoint-up(md) {
        margin: 0;
      }
    }
    @include media-breakpoint-up(md) {
      width: 32%;
      padding: 32px 25px 49px;
    }
    @include media-breakpoint-up(lg) {
      width: 32%;
      padding: 32px 40px 49px;
    }
    @include media-breakpoint-up(xl) {
      width: 32%;
      padding: 32px 48px 48px;
    }
  }
}

.cover-card {
  &__header {
    display: flex;
    align-items: center;
    margin-bottom: 16px;
  }
  &__icon {
    width: 24px;
    height: 24px;
    margin-right: 16px;
  }
  &__title {
    font-weight: bold;
    font-size: 22px;
    line-height: 36px;
    color: #181818;
    @include media-breakpoint-up(md) {
      font-size: 18px;
      line-height: 28px;
    }
    @include media-breakpoint-up(xl) {
      font-weight: 600;
      font-size: 22px;
      line-height: 36px;
    }
  }
  &__list {
    text-align: left;
  }
  &__item {
    display: flex;
    align-items: center;
    font-weight: normal;
    font-size: 16px;
    line-height: 26px;
    color: #181818;
    &:before {
      content: "";
      display: block;
      width: 6px;
      height: 6px;
      background-color: #ffe43c;
      top: 50%;
      left: 0;
      margin-right: 15px;
    }
    &:not(:last-child) {
      margin-bottom: 16px;
    }
    @include media-breakpoint-up(md) {
      font-size: 14px;
      line-height: 22px;
    }
    @include media-breakpoint-up(xl) {
      font-size: 16px;
      line-height: 26px;
    }
  }
}
