.step-by-step {
  overflow: hidden;
  background-color: #e5e5e5;
  padding: 0 15px 112px;
  @include media-breakpoint-up(lg) {
    padding: 0 0 240px;
  }
  &__heading {
    font-family: "Poppins", sans-serif;
    text-align: center;
    font-weight: 800;
    color: #181818;
    font-size: 28px;
    line-height: 44px;
    margin: 0 auto 56px;
    @include media-breakpoint-up(lg) {
      font-size: 48px;
      line-height: 76px;
      max-width: 565px;
      margin: 0 auto 48px;
    }
  }
  &__bullets {
    display: none;
    align-items: center;
    justify-content: space-between;
    @include media-breakpoint-up(lg) {
      display: flex;
      align-items: flex-start;
    }
  }
  &__bullet {
    background: transparent;
    border: none;
    outline: none;
    cursor: pointer;
    width: calc(20% - 15px);
    text-align: left;
  }
}

.glide__slide--active .step-by-step-slide__line:after {
  transform: scaleX(1);
  transition: all 9.5s linear;
}

.glide__bullet--active .step-by-step-slide__line:after {
  transform: scaleX(1);
  transition: all 9.5s linear;
}

.step-by-step-slide {
  display: block;
  &__content {
    @include media-breakpoint-up(lg) {
      display: none;
    }
  }
  &__img,
  &__picture {
    width: 100%;
    border-radius: 5px;
    overflow: hidden;
  }
  &__picture {
    margin-bottom: 26px;
    display: block;
    @include media-breakpoint-up(lg) {
      margin-bottom: 35px;
    }
  }
  &__icon {
    width: 24px;
    height: 24px;
    margin-bottom: 16px;
  }
  &__line {
    position: relative;
    width: 100%;
    height: 3px;
    background-color: #dddde1;
    margin-bottom: 16px;
    &:after {
      content: "";
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      width: 100%;
      transform: scaleX(0);
      transform-origin: left;
      background-color: #eed11b;
    }
  }
  &__title {
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-size: 18px;
    line-height: 28px;
    color: #181818;
    margin-bottom: 10px;
  }
  &__text {
    font-family: "Poppins", sans-serif;
    font-size: 13px;
    line-height: 20px;
    max-width: 260px;
    @include media-breakpoint-up(lg) {
      max-width: 100%;
    }
  }
}
